<template>

  <h1>VueX counter</h1>
  <h2 class="counter" >{{counter}}</h2>
  <button @click="DECREMENT_COUNTER(parseInt(value))">-</button>
  <input type="number" v-model='value'/>
  <button @click="INCREMENT_COUNTER(parseInt(value))">+</button>
  <div>
    <button class="btn" @click="addRandomNumber"> Add by Random Number</button>
  </div>
</template>

<script>
import {mapState, mapMutations, mapActions} from 'vuex'
export default {
  name: "Counter",
  data() {
    return {
      value: 1,
    };
  },
  computed: {
    ...mapState(['counter'])
  },
  methods: {
    ...mapMutations(['INCREMENT_COUNTER','DECREMENT_COUNTER']),
    ...mapActions(["addRandomNumber"])
  }
};
</script>

<style>
.counter {
  font-size: 5rem;
}

button {
  border-radius: 100%;
  border: none;
  width: 2rem;
  height: 2rem;
  font-weight: 700;
  cursor: pointer;
}
input {
  padding: 0.4rem;
  margin: 0 0.5rem;
  text-align: center;
}
.btn{
  border-radius: 0.5rem;
  width: auto;
  background: #41B983;
  margin-top: 1rem;
  color: white;
  cursor: pointer;
}
</style>
